<template>
	<div id="mapBgc">
		<div id="mapBox">
			<div id="map"></div>
			<div style="float: right;width: 255px;">
				<p style="color: #666;">  / contact</p>
				<p style="font-size: 12px;color:#888;margin-top: 30px;">800-0885-460</p>
				<p style="font-size: 12px;color:#888;margin-top: 10px;">www.qifeiye.com</p>
				<p style="font-size: 12px;color:#888;margin-top: 10px;">info@email.com</p>
				<p style="font-size: 12px;color:#888;margin-top: 10px;"></p>
				<p style="color: #666;margin-top: 50px;">  / business hours</p>
				<p style="font-size: 12px;color:#888;margin-top: 30px;">~</p>
				<p style="font-size: 12px;color:#888;margin-top: 10px;">10:00-14:00</p>
				<p style="font-size: 12px;color:#888;margin-top: 10px;">15:00-20:00</p>
				<p style="font-size: 12px;color:#888;margin-top: 10px;">~</p>
				<p style="font-size: 12px;color:#888;margin-top: 10px;">08:00-14:00</p>
				<p style="font-size: 12px;color:#888;margin-top: 10px;">15:00-19:00</p>
				
				
				
				
				
				
			</div>
			
		</div>
	</div>
</template>

<script>
	import BMap from 'BMap'
	export default{
		mounted() {
			var map = new BMap.Map("map");
			//   
			var point = new BMap.Point(116.404, 39.915);
			//   
			map.centerAndZoom(point, 10);
		
			//   
			map.enableScrollWheelZoom(true);
			//
			map.addControl(new BMap.NavigationControl());
		
			map.addControl(new BMap.ScaleControl());
			map.addControl(new BMap.OverviewMapControl());
			map.addControl(new BMap.MapTypeControl());
		}
	}
	</script>
</script>

<style>
	#mapBgc{
		background-color: #fbf8f8;
		overflow: hidden;
	}
	#mapBox{
		width: 1000px;
		height: 400px;
		margin: 30px auto;	
	}
	#map {
		height: 100%;
		width: 700px;
		float: left;
	}
</style>



