<template>
	<div>
		<div class="lbBox">
			<div class="lb">
				<span class="lbPrev" @click="prev">
					<
				</span>
				<span class="lbNext" @click="next">
					>
				</span>
				<div :style="'position: absolute;left: -'+n*550+'px;top:0;height: 100%;width:'+arr.length*550+'px;transition:.6s'">
					<div v-for="i in arr">
						<img :src="i.img" alt="">
						<div class="lbInfo">
							<div class="lbName">{{i.name}}</div>
							<div class="lbNum">{{i.num}}</div>
							<div class="lbTxt">{{i.txt}}</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

