				],
				n:0
			}
		},
		methods:{
			prev(){
				this.n--;
				if(this.n<0){
					this.n=0;
					return
				}
			},
			next(){
				this.n++;
				console.log(this.n,this.arr.length)
				if(this.n>this.arr.length-2){
					this.n=this.arr.length-2;
					return;
				}
			}
		}
	}
</script>

<style>
	.lbPrev,.lbNext{
		position: absolute;
		width: 32px;
		height: 32px;
		border-radius: 2px;
		background-color: #ccc;
		top: 50%;
		margin-top: -16px;
		text-align: center;
		line-height: 32px;
		color:#fff;
		cursor: pointer;
		z-index: 99;
		transition: .3s;
	}
	.lbPrev:hover,.lbNext:hover{
		background-color: #999;
	}
	.lbPrev{
		left: 0;
	}
	.lbNext{
		right: 0;
	}
	.lbBox{
		height: 380px;
		border-bottom: 1px solid #ccc;
		position: relative;
		
	}
	.lb img{
		width: 137px;
		height: 141px;
		float: left;
	}
	.lbInfo{
		width: 330px;
		height: 140px;
		float: left;
		box-sizing: border-box;
		padding-left: 20px;
		font-size: 10px;
		color:#999;
		position: relative;
		line-height: 20px;
	}
	.lbInfo .lbName{
		font-size: 16px;
		color:#333;
	}
	.lbTxt{
		position: absolute;
		bottom: 0;
		padding-right: 20px;
	}
	.lbInfo .lbNum{
		font-size: 12px;
		color:#666;
		margin-top: 5px;
		margin-bottom: 10px;
	}
	.lb{
		position: absolute;
		left: 50%;
		top:50%;
		width: 1100px;
		height: 180px;
		margin-top: -90px;
		margin-left: -550px;
		overflow: hidden;
	}
	.lb>div>div{
		float: left;
		width: 550px;
		height: 100%;
		box-sizing: border-box;
		padding-left: 45px;
		padding-top: 20px;
	}
</style>



